.benefits {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  max-width: 1194px;
  margin: auto;
  text-align: center;

  @media (max-width: 1150px) {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    justify-items: center;
  }

  @media (max-width: 800px) {
    display: flex;
    justify-content: center;

    & .delete {
      display: none;
    }
  }

  @media (max-width: 570px) {
    padding: 0 30px;
  }
}
